body {
  font-family: "Bitter", Georgia, "Times New Roman", Times, serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Luckiest Guy", Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

hero {
  display: block;
  position: relative;
  background-image: url("../assets/images/hero-buildings.png"), linear-gradient(#2b6869, #d2cfce);
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero-logo {
  z-index: 0;
  position: absolute;
  top: 0;
}

.hero-foreground {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 4;
}

.hero-squabs {
  z-index: 5;
  position: absolute;
  top: 0;
}

.canvas {
  left: 611px;
}

#park {
  z-index: 1;
}